.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 50px;
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 10px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.02);
}

.photo-card img {
  width: 100%;
  height: auto;
  max-height: 200px;
}

.info {
  margin-top: 10px;
}

.search-form {
  width: 100%;
  background-color: #fff;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
}

